import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { UtilService } from 'src/providers/utils/utils.service';
import { eNominationService } from './eNominationService';

@Component({
  selector: 'app-e-nomination',
  templateUrl: './e-nomination.page.html',
  styleUrls: ['./e-nomination.page.scss'],
})
export class ENominationPage implements OnInit {
  eNominationLink :any;
  canTryAgain = false;
  constructor(
    public logger: LoggerServiceProvider,
    public utils: UtilService, public geteNominationService: eNominationService,public translate:TranslateService
  ) {
    this.utils.analytics("ENominationPage");
  }

  ngOnInit() {
    this.getENominationlink();
  }
  // ionViewDidLoad() {
  //   console.log('ionViewDidLoad ENominationPage');
  //    //this.getENominationlink();
  // }
  getENominationlink = function () {
    if (this.utils.isDeviceOnLine()) {
      this.canTryAgain = false;
    this.utils.showProgressLoader();
      this.errorMsg = "";
      this.logger.trace("get getEnominationPath--start");
      this.geteNominationService.getEnominationPath().then(
        (result) => {
          try{
            this.eNominationLink = result['responseJSON'].url;
            this.utils.openLink(this.eNominationLink);
            this.utils.dissmisProgressLoader();
          }catch(e){
			      this.utils.dissmisProgressLoader();
            this.canTryAgain = true;
            this.errorMsg = this.translate.instant('generic_msgs.server_err');
          }
        }, (err) => {
		  this.utils.dissmisProgressLoader();
          this.canTryAgain = true;
          this.errorMsg = this.utils.handleServiceException(err);
        });
    } else {
	  this.utils.dissmisProgressLoader();
      this.canTryAgain = true;
      this.errorMsg = this.translate.instant('generic_msgs.network_err');
    }

  }

}
